import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, compareJSON, fetchEncryptStream } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();


const frmNm = "upd_product";

const Updproduct  = ({data, confirm}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};



	const save  = async ()  => {
		let ffm = getStore(frmNm);
		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

		let bdyq2 = {
			query: "updateProductsHRG",
			params:{
				"productId": ffm["productId"],
				businessId:  getStore("profileUser")?.businessId
			},
			data2update: diff.data
		};
		
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};

	let flds = ["unit","name", "description", "productCode", "hasDimension", "brand", "imageUrl"  ]

	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )




	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	
	

	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Producto
				</p>
				<div className="flexSpace"/>
				{getStore(frmNm)?.["cloverId"]}
			</div>

			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>
			<div className="_dsplFlx ">
				
				<div className="flexSpace"/>
				<div className="_dsplFlx " style={{marginTop: 17}}>
				
					<CheckBoxSlide
						initvalue={getStore(frmNm)?.["hasDimension"]}
						keyCode={77}
						updChange={(e)=>updFrm("hasDimension",e)}
					/>
					<span className={`checklabel`}>
						{"Necesita Dimensiones"}
					</span>
				</div>
			</div>
			<div className="_dsplFlx " style={{marginTop: 7}}>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["unit"]} 
					label={"Unidad de medida"} 
					width={120} 
					updChanges={(e)=>updFrm("unit",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["brand"]} 
					label={"Marca"} 
					width={120} 
					updChanges={(e)=>updFrm("brand",e)}
				/>
				
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["productCode"]} 
					label={"U.P.C"} 
					width={120} 
					updChanges={(e)=>updFrm("productCode",e)}
				/>
				<div className="flexSpace"/>
				
			</div>
			<div className="_dsplFlx " style={{marginTop: 7}}>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["imageUrl"]} 
					label={"Imagen Url"} 
					width={520} 
					updChanges={(e)=>updFrm("imageUrl",e)}
				/>
				<div className="flexSpace"/>
			</div>

			
			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{diff?.hasChange ?
				<div className="card">
					<button onClick={save}>
						Actualizar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default Updproduct;
